body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;

  /* font-size: 30px; */
  background-color: rgba(167, 214, 251, 0.7);
}
.slick-dots li button::before {
  font-size: 24px !important;
}
.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 24px !important;
  font-weight: bold !important;
  line-height: 1;
  opacity: 0.75;
  color: black !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.img_custom {
  height: 150px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
.card_details {
  min-height: 150px;
  max-height: 150px;
}
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 200px;
  height: 350px;
  border-radius: 0.5rem;
  margin: 1rem;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
